* {
    box-sizing: border-box;
}

.header {
    padding: 10px 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, .4);

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.navigation {
    display: flex;
    margin: 0 auto;
    padding: 0;
    list-style: none;

    li {
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }

        a {
            padding: 5px;

            &.active {
                color: #000;
                text-decoration: none;
            }
        }
    }
}

.header-logo {
    height: 50px;
    width: 50px;
    background-image: url('./images/js.png');
    background-size: contain;
}

.content {
    padding: 40px 0;
}

.not-found {
    margin: 0 15px;
}

.modal {
    .form-group {
        margin-bottom: 20px;
    }
}

@keyframes fadePositive {
    from { 
        background-color: green;
    }

    to { 
        background-color: transparent;
    }
}

@keyframes fadeNegative {
    from { 
        background-color: tomato;
    }

    to { 
        background-color: transparent;
    }
}

.table {
    td {
        padding: 1rem .5rem;
    }

    tr:nth-child(2n) {
        background-color: rgba(#eee, .5);
    }

    .positive {
        animation: fadePositive 2s ease-in-out 1;

        .persent {
            color: green;
        }
    }

    .negative {
        animation: fadeNegative 2s ease-in-out 1;

        .persent {
            color: red;
        }
    }

    img {
        width: 20px;
        margin: 0 7px;
    }

    .spinner {
        text-align: center;
    }

    tbody {
        background-color: rgba(#eee, .3);
    }

    thead {
        position: sticky;
        top: 0;
        left: 0;
        background-color: #fff;

        button {
            border: 0;

            &.ascending::after {
                content: 'üëá';
                display: inline-block;
                margin-left: 10px;
            }

            &.descending::after {
                content: '‚òùÔ∏è';
                display: inline-block;
                margin-left: 1em;
            }
        }
    }
}

.coin-logo {
    width: 200px;
    display: block;
}

.crypto-title {
    margin-bottom: 40px;
}

.is-danger {
    color: red;
    margin-bottom: 0;
}

.input.is-danger {
    border-color: red;
}

.crypto-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3rem;
}

.pagination-main {
    margin-top: 3rem;

    .page-item.disabled span {
        background-color: #ccc;
        color: #fff;
    }
}

.login {
    display: flex;
    align-items: center;
}

.user-name {
    margin-right: 1.5rem;
}

.modal-error {
    color: red;
    font-size: 22px;
}

.spinner-border {
    display: block;
    margin: 0 auto;
}

.weather-container {
    max-width: 500px;

    input {
        margin-right: 15px;
    }
}

.weather-list {
    margin: 0;
    padding: 0;

    li {
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    span {
        display: block;
        margin-left: 15px;
    }

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }

    &__item {
        display: flex;
        align-items: center;
    }

    i {
        font-size: 20px;
    }
}

.weather-list-week {
    padding: 0;
    margin: 30px 0 0;
    list-style: none;
    display: flex;

    li {
        flex-grow: 1;
        text-align: center;

        div {
            margin: 5px 0;
        }

        .temperature {
            font-size: 11px;
            margin: 0;
        }
    }
}
